.card {
    overflow: hidden;
    position: relative;
    text-align: center;
    padding: 0px;
    background: #FFF;
    box-shadow: 0px 0px 5px #555;
    filter: drop-shadow(0px 0px 5px #555);
    -webkit-filter: drop-shadow(0px 0px 5px #555);
    filter: drop-shadow(0px 0px 5px #555);
    border-radius: 10%;
    margin-left: 5%;
    margin-right: 1%;
    margin-top: 16%;
    margin-bottom: -5%;
}

.card .header-bg {
    position: absolute;
    width: 100%;
    height: 40%;
    z-index: 1;
}

.card .avatar {
    position: relative;
    margin-top: 4%;
    z-index: 100;
}

.card .avatar img {
    width: 141px;
    height: 141px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}

.friendly {
    padding: 0px;
    background: #FFF;
    box-shadow: 0px 0px 5px #555;
    filter: drop-shadow(0px 0px 5px #555);
    -webkit-filter: drop-shadow(0px 0px 5px #555);
    filter: drop-shadow(0px 0px 5px #555);
    border-radius: 10%;
}

#rightcontainer {
    background-color: #E4EBF3;
    min-height: 600px;
    width: 100%;
}

.leftcontainer {
    background-color: #292d32;
    height: 600px;
}

.nav-padding {
    padding: 22%
}

html {
    min-height: 100%;
}

body {
    font-family: system-ui;
    /* background-image: linear-gradient(#ffffff, rgb(172, 172, 134), #796155); */
    /* background: #fff */
}

.nav-prop {
    background-color: #292d32;
    margin-top: -15%;
}

.circleonclick {
    height: 30px;
    width: 30px;
    display: table-cell;
    vertical-align: middle;
    border-radius: 50%;
    background: #4FBF65;
    margin: 0px 7px 0px 0px;
    font-size: 20px;
    text-align: center;
}

.circlebeforeclick {
    height: 30px;
    width: 30px;
    display: table-cell;
    vertical-align: middle;
    border-radius: 50%;
    background: #626c76;
    margin: 0px 7px 0px 0px;
    font-size: 20px;
    text-align: center;
}

.circleafterclick {
    height: 30px;
    width: 30px;
    display: table-cell;
    vertical-align: middle;
    border-radius: 50%;
    background: #6c70dc;
    margin: 0px 7px 0px 0px;
    font-size: 20px;
    text-align: center;
}

.nameproperty {
    display: inline-table;
    width: 110%;
    height: 100%;
    margin: 0px 0px 30px;
    color: #FFFFFF
}

.selected {
    color: #4FBF65;
}

.non-selected {
    color: #FFFFFF;
}

.nav-link {
    color: white;
}

#myclass {
    color: white
}

.img-fluid {
