}


.datepickr, .timepickr {
    background-color: greenyellow;
    text-align: left;
    width: 690px;
}

.dateandtimediv {
    display: inline-block;
    margin: 0px 0px 15px;
    height: 76px;
    width: 90%;
    padding: 0px;
    border-radius: 2px;
    padding: 13px 20px;
    margin-top: 0%;
}

#dateandtimediv {
    display: inline-block;
    margin: 0px 0px 15px;
    height: 76px;
    width: 100%;
    background: #FFF;
    box-shadow: 0px 0px 5px #555;
    filter: drop-shadow(0px 0px 5px #555);
    -webkit-filter: drop-shadow(0px 0px 5px #555);
    filter: drop-shadow(0px 0px 5px #555);
    border-radius: 2px;
    padding: 13px 20px;
    margin-top: 35%;
}

/* information */

#informationdiv {
    display: inline-block;
    margin: 0px 0px 15px;
    height: 76px;
    width: 95%;
    padding: 0px;
    background: #FFF;
    box-shadow: 0px 0px 5px #555;
    filter: drop-shadow(0px 0px 5px #555);
    -webkit-filter: drop-shadow(0px 0px 5px #555);
    filter: drop-shadow(0px 0px 5px #555);
    border-radius: 2px;
    padding: 13px 20px;
    margin-top: 33%;
    margin-left: 2%;
}

.date-time {
    width: 49%;
}

/* removing arrow from no field */

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */

input[type=number] {
    -moz-appearance: textfield;
}

#h3_ {
    color: green;
}

a:hover {
    color: inherit;
}

